@import url('https://fonts.googleapis.com/css2?family=Open+Sans&family=Outfit:wght@100;200;300;400;500;600;700;800;900&family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,600;0,800;1,100;1,700&display=swap');
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Outfit', sans-serif;
}
body{
  background: black;
}
h1,h4,p,a{
  color: #ffff;
  text-decoration: none;

}
ul{
  list-style-type: none;
}
.btn{
  padding: 12px 32px;
  font-size: 1.5rem;
  text-transform: lowercase;
  background-color: #dac511;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
}
.btn-light{
  background: transparent;
  color: #fff;
}
.btn:hover{
  background:rgba(255,255,255, 0.2);
  color: #fff;
  transition: 0.3s;
}