.hero-img {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5, );
    height: 60vh;
    padding: relative;
}

.hero-img::before {
    content: "";
    background: url("https://miro.medium.com/v2/resize:fit:828/format:webp/1*E0AtatuxAJ1saRXZaF8BlQ.jpeg");
    background-size: cover;
    background-position: center top;
    height: 60vh;
    width: 100%;
    position: absolute;
    top:0;
    left: 0;
    z-index: -1;
    
}
.hero-img::after {
    content: "";
    width: 100%;
    height: 60vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.6;
}

